<script>
import KpInvoice from "./components/Invoice.vue";
import KpHeader from "./components/Header.vue";
import KpFooter from "./components/Footer.vue";
import DaerahPopup from "./components/DaerahPopup.vue";
import { store, mutations, getters } from "./store";
import VueOtpInput from "@bachdgvn/vue-otp-input";

export default {
  name: "app",
  components: {
    KpInvoice,
    KpHeader,
    KpFooter,
    DaerahPopup,
    OtpField: VueOtpInput
  },
  data() {
    return {
      hasPrev: false,
      done: null,
      showOtp: false,
      otpDone: false,
      timeleft: 0,
      countSendedOtp: 0
    };
  },
  computed: {
    hasBranch() {
      return store.hasBranch;
    }
  },
  methods: {
    sendOtp() {
      this.showOtp = true;
      this.startCountdown();
    },
    otpCompleted(val) {
      this.otpDone = true;
      mutations.setOtpCode(`${val}`);
    },
    closekeun() {
      if (this.done === "error") {
        this.done = null;
      }
    },
    validate(step, data) {
      this.$root.stepLoading = true;
      return this.$http
        .post("api/v1/validate/" + step, {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          },
          json: data
        })
        .then(res => res.json())
        .catch(err => err.response.json());
    },
    nextKeun() {
      let activeComp = null;
      let currentData = null;
      let endPoint = null;
      let cb = null;
      if (
        (this.$root.cur === 0 && this.$refs.activeForm.cur < 2) ||
        (this.$root.cur === 2 && this.$refs.activeForm.cur < 1)
      ) {
        activeComp = this.$refs.activeForm.$children[this.$refs.activeForm.cur];
        currentData = this.$refs.activeForm.$children[this.$refs.activeForm.cur]
          ._self.fixedData;
        endPoint = store.realStep[this.$root.cur][this.$refs.activeForm.cur];
        cb = finalData => {
          if (
            this.$root.cur === 0 &&
            this.$refs.activeForm.cur === 1 &&
            (currentData.home_status === "KS" ||
              currentData.home_status === "KR") &&
            (currentData.rent_finish_date === "" ||
              currentData.rent_finish_date === null)
          ) {
            finalData.data.items = Object.assign(finalData.data.items || {}, {
              rent_finish_date: ["The rent_finish_date field is required"]
            });
            finalData.data.message = "Validation Error";
          }
          if (
            this.$root.cur === 2 &&
            this.$refs.activeForm.cur === 0 &&
            currentData.profession_id === "WRST"
          ) {
            for (let index = 1; index <= 3; index++) {
              if (
                currentData[`monthly_omset_${index}_month`] === "" ||
                currentData[`monthly_omset_${index}_month`] === null ||
                currentData[`monthly_omset_${index}_year`] === "" ||
                currentData[`monthly_omset_${index}_year`] === null
              ) {
                const objOmset = {};
                objOmset[`monthly_omset_${index}`] = [
                  `The monthly_omset_${index} field is required`
                ];
                finalData.data.items = Object.assign(
                  finalData.data.items || {},
                  objOmset
                );
                finalData.data.message = "Validation Error";
              }
              if (
                currentData[`monthly_omset_${index}_omset`] === "" ||
                currentData[`monthly_omset_${index}_omset`] === null
              ) {
                const objOmset = {};
                objOmset[`monthly_omset_${index}_omset`] = [
                  `The monthly_omset_${index}_omset field is required`
                ];
                finalData.data.items = Object.assign(
                  finalData.data.items || {},
                  objOmset
                );
                finalData.data.message = "Validation Error";
              }
            }
          }
          if (finalData.data.message === "Success") {
            this.$refs.activeForm.cur += 1;
            window.scrollTo({ top: 100, left: 0, behavior: "smooth" });
            if (this.$refs.activeForm.cur > 0) {
              this.hasPrev = true;
            } else {
              this.hasPrev = false;
            }
            return { index: endPoint, data: currentData };
          } else {
            throw finalData.data.items;
          }
        };
      } else {
        if (this.$root.cur === 0 || this.$root.cur === 2) {
          activeComp = this.$refs.activeForm.$children[
            this.$refs.activeForm.cur
          ];
          currentData = this.$refs.activeForm.$children[
            this.$refs.activeForm.cur
          ]._self.fixedData;
          endPoint = store.realStep[this.$root.cur][this.$refs.activeForm.cur];
        } else {
          activeComp = this.$refs.activeForm;
          currentData = this.$refs.activeForm._self.fixedData;
          endPoint = store.realStep[this.$root.cur];
        }
        cb = finalData => {
          if (finalData.data.message === "Success") {
            this.$root.cur += 1;
            window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
            return { index: endPoint, data: currentData };
          } else {
            throw finalData.data.items;
          }
        };
      }
      this.validate(endPoint, currentData)
        .then(cb)
        .then(data => {
          mutations.setStepVal(data);
        })
        .catch(err => {
          // ['stay_since_month', 'stay_since_year'];
          const errMsg = {};
          err = Object.keys(err).reduce((fixedArr, errorna) => {
            if (
              errorna === "stay_since_month" ||
              errorna === "stay_since_year"
            ) {
              fixedArr.includes("rent_since")
                ? fixedArr
                : fixedArr.push("rent_since");
              errMsg["rent_since"] = err[errorna];
              return fixedArr;
            }
            fixedArr.push(errorna);
            errMsg[errorna] = err[errorna];
            return fixedArr;
          }, []);
          activeComp.errorFields = err;
          activeComp.errorMessages = errMsg;
          // console.log(err, errMsg);
          try {
            document
              .querySelector("#" + err[0])
              .scrollIntoView({ behavior: "smooth", block: "center" });
          } catch (error) {
            return false;
          }
        })
        .then(() => {
          this.$root.stepLoading = false;
        });
    },
    startCountdown() {
      if (this.timeleft === 0 && this.countSendedOtp < 3) {
        this.countSendedOtp += 1;
        const noHp = store.stepVal["contact-page"].mobile_phone.replace(
          /^0/,
          "+62"
        );
        mutations.setDestination(noHp);
        this.$http.get(`api/v1/otp/send/${noHp}`, {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        });
        this.timeleft = 60;
        const downloadTimer = setInterval(() => {
          this.timeleft -= 1;
          if (this.timeleft <= 0) {
            clearInterval(downloadTimer);
          }
        }, 1000);
      }
    },
    prevKeun() {
      if (
        this.$root.cur === 0 ||
        (this.$root.cur === 2 && this.$refs.activeForm.cur > 0)
      ) {
        window.scrollTo({ top: 100, left: 0, behavior: "smooth" });
        this.$refs.activeForm.cur -= 1;
        if (this.$refs.activeForm.cur > 0 || this.$root.cur === 2) {
          this.hasPrev = true;
        } else {
          this.hasPrev = false;
        }
      } else {
        this.$root.cur -= 1;
        window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
      }
    },
    submitForm() {
      if (!this.otpDone) {
        return;
      }
      this.$root.stepLoading = true;
      this.showOtp = false;
      const bodyReq = getters.dataSubmit();
      return this.$http
        .post("api/v1/pengajuan-mitra/send-data", {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          },
          json: bodyReq
        })
        .then(res => res.json())
        .then(dataRes => {
          if (dataRes.data.message === "Success") {
            this.$root.stepLoading = false;
            this.done = "sukses";
          } else {
            return Promise.reject(dataRes.data.message);
          }
        })
        .catch(async err => {
          this.$root.stepLoading = false;
          this.showOtp = true;
          if (err.message) {
            err = await err.response.json();
            err = err.data.message;
          }
          this.otpDone = false;
          mutations.setOtpCode("");
          this.done = err;
        });
    }
  }
};
</script>

