<template>
  <div class="mb-2 w-full">
    <label class="block text-grey-darker text-sm font-bold mb-2" :for="idna">
      {{ label }}
    </label>
    <textarea
      rows="5"
      class="shadow appearance-none border rounded w-full py-2 px-3 text-grey-darker leading-tight focus:outline-none"
      :class="{ 'border-kp-500': isError }"
      :id="idna"
      :placeholder="label"
      :disabled="disabled"
      v-model="tempVal"
      @input="pushVal"
    />
    <ul v-if="isError" class="list-disc list-inside text-kp-500 mt-1">
      <li v-for="(err, i) in errorList" :key="i">
        {{ err.replace(new RegExp(".+(?= field)"), label) }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: ""
    },
    label: {
      type: String,
      default: ""
    },
    id: {
      type: String,
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    },
    hasError: {
      type: [Array, Boolean],
      default() {
        return null;
      }
    }
  },
  watch: {
    value(val) {
      this.tempVal = val;
    },
    hasError(val) {
      if (val.length > 0) {
        this.errorList = val;
        this.isError = true;
      } else {
        this.isError = false;
      }
    }
  },
  data() {
    return {
      tempVal: this.value,
      isError: this.hasError,
      errorList: []
    };
  },
  computed: {
    idna() {
      return this.id || this.label.replace(/ /, "");
    }
  },
  methods: {
    pushVal(e) {
      this.$emit("input", e.target.value);
      if (this.isError) {
        this.isError = false;
      }
    }
  }
};
</script>
