<template>
  <div
    class="block sm:flex md:block lg:flex items-center justify-center flex-col px-6 py-4"
  >
    <div class="flex justify-center items-center mb-4 flex-wrap">
      <span class="text-lg font-bold">Lampiran</span>
      <span class="w-full text-center">
        Lengkapi Lampiran dokumen identitas anda.
      </span>
    </div>
    <input-video
      v-model="base_64_foto_ktp"
      label="Foto KTP"
      class="w-full"
      :has-error="
        errorFields.includes('base_64_foto_ktp')
          ? errorMessages['base_64_foto_ktp']
          : false
      "
      id="base_64_foto_ktp"
    />
    <input-video
      v-model="base_64_selfie_ktp"
      label="Foto Diri & KTP"
      class="w-full"
      :has-error="
        errorFields.includes('base_64_selfie_ktp')
          ? errorMessages['base_64_selfie_ktp']
          : false
      "
      id="base_64_selfie_ktp"
    />
    <input-video
      v-model="base_64_close_up"
      label="Foto Close Up"
      class="w-full"
      :has-error="
        errorFields.includes('base_64_close_up')
          ? errorMessages['base_64_close_up']
          : false
      "
      id="base_64_close_up"
    />
    <input-video
      v-model="base_64_persyaratan_kredit"
      label="Document Persyaratan Kredit Lainnya"
      class="w-full"
      file
      id="base_64_persyaratan_kredit"
    />
    <input-video
      v-model="base_64_lainnya"
      label="Document Lainnya"
      class="w-full"
      file
      id="base_64_lainnya"
    />
  </div>
</template>

<script>
import InputVideo from "./InputVideo.vue";
// eslint-disable-next-line no-unused-vars
import { store } from "../store";

export default {
  components: {
    InputVideo
  },
  data() {
    return {
      base_64_foto_ktp: null,
      base_64_selfie_ktp: null,
      base_64_close_up: null,
      base_64_persyaratan_kredit: null,
      base_64_lainnya: null,
      errorFields: []
    };
  },
  computed: {
    fixedData() {
      const indexNa = [
        "foto_ktp",
        "selfie_ktp",
        "close_up",
        "persyaratan_kredit",
        "lainnya"
      ];
      const order_image = indexNa.reduce((fixed, fotoNa, i) => {
        if (this[`base_64_${fotoNa}`] && this[`base_64_${fotoNa}`] !== "") {
          fixed[`doc_no_${fotoNa}`] = i + 1;
          fixed[`base_64_${fotoNa}`] = this[`base_64_${fotoNa}`].replace(
            "data:image/jpeg;base64,",
            ""
          );
          fixed[
            `file_name_${fotoNa}`
          ] = `${fotoNa}_${store.stepVal["contact-page"].email}.jpg`;
          // fixed[`file_name_${fotoNa}`] = `${fotoNa}_.jpg`;
        }
        return fixed;
      }, {});
      return order_image;
    }
  }
};
</script>
