<template>
  <div class="block sm:flex md:block lg:flex justify-center flex-col px-6 py-4">
    <div class="flex justify-center items-center mb-4 flex-wrap">
      <span class="text-lg font-bold">Alamat Perusahaan</span>
      <span class="w-full text-center">
        Masukkan alamat lengkap perusahaan.
      </span>
    </div>
    <text-area
      label="Alamat Perusahaan (Lengkap)"
      tag="textarea"
      v-model="company_address"
      id="company_address"
      :has-error="
        errorFields.includes('company_address')
          ? errorMessages['company_address']
          : false
      "
    />
    <div class="w-full flex">
      <input-field
        label="RT"
        class="w-1/2 inline-block mr-4"
        v-model="company_rt"
        numeric
        max="3"
        :has-error="
          errorFields.includes('company_rt')
            ? errorMessages['company_rt']
            : false
        "
      />
      <input-field
        label="RW"
        class="w-1/2 inline-block"
        v-model="company_rw"
        numeric
        max="3"
        :has-error="
          errorFields.includes('company_rw')
            ? errorMessages['company_rw']
            : false
        "
      />
    </div>
    <select-field
      :options="listProvinsi"
      label="Provinsi"
      v-model="company_province"
      value-field="province"
      text-field="province"
      :has-error="
        errorFields.includes('company_provinsi')
          ? errorMessages['company_provinsi']
          : false
      "
    />
    <select-field
      :options="listKota"
      :disabled="kotaDisabled"
      label="Kota / Kabupaten"
      v-model="company_city"
      value-field="city"
      text-field="city"
      :has-error="
        errorFields.includes('company_city')
          ? errorMessages['company_city']
          : false
      "
    />
    <select-field
      :options="listKecamatan"
      :disabled="kecamatanDisabled"
      label="Kecamatan"
      v-model="company_kecamatan"
      value-field="kecamatan"
      text-field="kecamatan"
      :has-error="
        errorFields.includes('company_kecamatan')
          ? errorMessages['company_kecamatan']
          : false
      "
    />
    <select-field
      :options="listKelurahan"
      :disabled="kelurahanDisabled"
      label="Kelurahan / Desa"
      v-model="company_kelurahan"
      value-field="kelurahan"
      text-field="kelurahan"
      :has-error="
        errorFields.includes('company_kelurahan')
          ? errorMessages['company_kelurahan']
          : false
      "
    />
    <input-field
      label="Kode Pos"
      class="w-full"
      v-model="company_zip_code"
      disabled
      numeric
      :has-error="
        errorFields.includes('company_zip_code')
          ? errorMessages['company_zip_code']
          : false
      "
    />
    <div class="flex w-full">
      <input-field
        label="Kode Area"
        class="w-32 mr-4"
        v-model="company_area_phone_1"
        id="company_area_phone_1"
        max="4"
        numeric
        :has-error="
          errorFields.includes('company_area_phone_1')
            ? errorMessages['company_area_phone_1']
            : false
        "
      />
      <input-field
        label="Telepon Kantor"
        class="w-full"
        v-model="company_phone_1"
        id="company_phone_1"
        max="10"
        numeric
        :has-error="
          errorFields.includes('company_phone_1')
            ? errorMessages['company_phone_1']
            : false
        "
      />
    </div>
  </div>
</template>

<script>
import TextArea from "./TextArea.vue";
import InputField from "./InputField.vue";
import SelectField from "./SelectField.vue";

import { store } from "../store";

export default {
  components: {
    InputField,
    SelectField,
    TextArea
  },
  data() {
    return {
      unwatchProvince: null,
      unwatchCity: null,
      unwatchKecamatan: null,
      unwatchKelurahan: null,
      company_address: null,
      company_rt: null,
      company_rw: null,
      company_province: null,
      listKota: [],
      kotaDisabled: true,
      company_city: null,
      listKecamatan: [],
      kecamatanDisabled: true,
      company_kecamatan: null,
      listKelurahan: [],
      kelurahanDisabled: true,
      company_kelurahan: null,
      company_zip_code: null,
      company_area_phone_1: null,
      company_phone_1: null,
      errorFields: []
    };
  },
  methods: {
    change_company_province(val) {
      this.company_city = null;
      if (val) {
        this.$http
          .get(`api/v1/master/city/${val}`, {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          })
          .then(res => res.json())
          .then(resKota => {
            this.listKota = resKota.data.items;
            this.kotaDisabled = false;
          });
      } else {
        this.listKota = [];
        this.kotaDisabled = true;
      }
    },
    change_company_city(val) {
      this.company_kecamatan = null;
      if (val) {
        this.$http
          .get(`api/v1/master/kecamatan/${this.company_province}/${val}`, {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          })
          .then(res => res.json())
          .then(resKecamatan => {
            this.listKecamatan = resKecamatan.data.items;
            this.kecamatanDisabled = false;
          });
      } else {
        this.listKecamatan = [];
        this.kecamatanDisabled = true;
      }
    },
    change_company_kecamatan(val) {
      this.company_kelurahan = null;
      if (val) {
        this.$http
          .get(
            `api/v1/master/kelurahan/${this.company_province}/${this.company_city}/${val}`,
            {
              withCredentials: true,
              headers: {
                Authorization: `${this.$root.getParameterByName("token")}`
              }
            }
          )
          .then(res => res.json())
          .then(resKecamatan => {
            this.listKelurahan = resKecamatan.data.items;
            this.kelurahanDisabled = false;
          });
      } else {
        this.listKelurahan = [];
        this.kelurahanDisabled = true;
      }
    },
    change_company_kelurahan(val) {
      this.company_zip_code = null;
      if (val) {
        this.$http
          .get(
            `api/v1/master/zip/${this.company_province}/${this.company_city}/${this.company_kecamatan}/${val}`,
            {
              withCredentials: true,
              headers: {
                Authorization: `${this.$root.getParameterByName("token")}`
              }
            }
          )
          .then(res => res.json())
          .then(resKelurahan => {
            this.company_zip_code = resKelurahan.data.item[0].zipcode;
          });
      } else {
        this.company_zip_code = null;
      }
    }
  },
  mounted() {
    this.unwatchProvince = this.$watch(
      "company_province",
      this.change_company_province
    );
    this.unwatchCity = this.$watch("company_city", this.change_company_city);
    this.unwatchKecamatan = this.$watch(
      "company_kecamatan",
      this.change_company_kecamatan
    );
    this.unwatchKelurahan = this.$watch(
      "company_kelurahan",
      this.change_company_kelurahan
    );
    if (process.env.NODE_ENV === "development") {
      this.company_address = "Jl. Khintil Hilih No.123";
      this.company_rt = "09";
      this.company_rw = "10";
      this.company_province = "DKI JAKARTA";
      this.company_area_phone_1 = "021";
      this.company_phone_1 = "12334555";
    }
  },
  computed: {
    listProvinsi() {
      return store.province;
    },
    fixedData() {
      return {
        company_address: this.company_address,
        company_rt: this.company_rt,
        company_rw: this.company_rw,
        company_province: this.company_province,
        company_city: this.company_city,
        company_kecamatan: this.company_kecamatan,
        company_kelurahan: this.company_kelurahan,
        company_zip_code: this.company_zip_code,
        company_area_phone_1: this.company_area_phone_1,
        company_phone_1: this.company_phone_1
      };
    }
  }
};
</script>
