<template>
  <div class="block sm:flex md:block lg:flex justify-center flex-col px-6 py-4">
    <div class="flex justify-center items-center mb-4 flex-wrap">
      <span class="text-lg font-bold">Alamat</span>
      <span class="w-full text-center">
        Lengkapi alamat lengkap anda.
      </span>
    </div>
    <div
      class="-mx-6 mb-3 font-medium items-center bg-gray-300 leading-none py-4"
    >
      <span class="inline-flex px-6">
        Alamat Tinggal
      </span>
    </div>
    <text-area
      label="Alamat Tinggal Sekarang (Lengkap)"
      tag="textarea"
      v-model="residence_address"
      id="residence_address"
      :has-error="
        errorFields.includes('residence_address')
          ? errorMessages['residence_address']
          : false
      "
    />
    <div class="w-full flex">
      <input-field
        label="RT"
        class="w-1/2 inline-block mr-4"
        v-model="residence_rt"
        id="residence_rt"
        numeric
        max="3"
        :has-error="
          errorFields.includes('residence_rt')
            ? errorMessages['residence_rt']
            : false
        "
      />
      <input-field
        label="RW"
        class="w-1/2 inline-block"
        v-model="residence_rw"
        id="residence_rw"
        numeric
        max="3"
        :has-error="
          errorFields.includes('residence_rw')
            ? errorMessages['residence_rw']
            : false
        "
      />
    </div>
    <input-field
      label="Provinsi"
      v-model="residence_province"
      id="residence_province"
      disabled
    />
    <input-field
      label="Kota / Kabupaten"
      v-model="residence_city"
      id="residence_city"
      disabled
    />
    <input-field
      label="Kecamatan"
      v-model="residence_kecamatan"
      id="residence_kecamatan"
      disabled
    />
    <input-field
      label="Kelurahan / Desa"
      v-model="residence_kelurahan"
      id="residence_kelurahan"
      disabled
    />
    <!-- <select-field
      :options="listProvinsi"
      label="Provinsi"
      v-model="residence_province"
      value-field="province"
      text-field="province"
    />
    <select-field
      :options="listKota"
      :disabled="kotaDisabled"
      label="Kota / Kabupaten"
      v-model="residence_city"
      value-field="city"
      text-field="city"
    />
    <select-field
      :options="listKecamatan"
      :disabled="kecamatanDisabled"
      label="Kecamatan"
      v-model="residence_kecamatan"
    />
    <select-field
      :options="listKelurahan"
      :disabled="kelurahanDisabled"
      label="Kelurahan / Desa"
      v-model="residence_kelurahan"
    /> -->
    <input-field
      label="Kode Pos"
      class="w-full"
      v-model="residence_zip_code"
      id="residence_zip_code"
      disabled
      numeric
      :has-error="
        errorFields.includes('residence_zip_code')
          ? errorMessages['residence_zip_code']
          : false
      "
    />
    <select-field
      :options="listHouses"
      label="Status Rumah"
      v-model="home_status"
      id="home_status"
      value-field="id_confins"
      text-field="description"
      :has-error="
        errorFields.includes('home_status')
          ? errorMessages['home_status']
          : false
      "
    />
    <input-date
      label="Tinggal Sejak"
      class="w-full"
      v-model="rent_since"
      id="rent_since"
      :has-error="
        errorFields.includes('rent_since') ? errorMessages['rent_since'] : false
      "
      :disabled-dates="{ from: new Date() }"
    />
    <input-date
      v-if="home_status === 'KS' || home_status === 'KR'"
      label="Tinggal Sampai"
      class="w-full"
      full-date
      v-model="rent_finish_date"
      id="rent_finish_date"
      :disabled="!rent_since"
      :has-error="
        errorFields.includes('rent_finish_date')
          ? errorMessages['rent_finish_date']
          : false
      "
      :disabled-dates="{
        to: new Date()
      }"
    />
    <div
      class="-mx-6 mb-3 mt-2 font-medium items-center bg-gray-300 leading-none py-4"
    >
      <span class="inline-flex px-6">
        Alamat Sesuai KTP
      </span>
    </div>
    <check-box label="Sama dengan alamat tinggal" v-model="alamatIsKtp" />
    <text-area
      v-if="!alamatIsKtp"
      id="legal_address"
      v-model="legal_address"
      label="Alamat sesuai KTP"
      tag="textarea"
      class="mt-4"
      :has-error="
        errorFields.includes('legal_address')
          ? errorMessages['legal_address']
          : false
      "
    />
    <div v-if="!alamatIsKtp" class="w-full">
      <input-field
        id="legal_rt"
        v-model="legal_rt"
        label="RT"
        class="w-20 inline-block mr-4"
        numeric
        max="3"
        :has-error="
          errorFields.includes('legal_rt') ? errorMessages['legal_rt'] : false
        "
      />
      <input-field
        id="legal_rw"
        v-model="legal_rw"
        label="RW"
        class="w-20 inline-block"
        max="3"
        numeric
        :has-error="
          errorFields.includes('legal_rw') ? errorMessages['legal_rw'] : false
        "
      />
    </div>
    <select-field
      v-if="!alamatIsKtp"
      :options="listProvinsi"
      label="Provinsi"
      v-model="legal_province"
      id="legal_province"
      value-field="province"
      text-field="province"
      :has-error="
        errorFields.includes('legal_province')
          ? errorMessages['legal_province']
          : false
      "
    />
    <select-field
      v-if="!alamatIsKtp"
      :options="listKota"
      :disabled="kotaDisabled"
      label="Kota / Kabupaten"
      v-model="legal_city"
      id="legal_city"
      value-field="city"
      text-field="city"
      :has-error="
        errorFields.includes('legal_city') ? errorMessages['legal_city'] : false
      "
    />
    <select-field
      v-if="!alamatIsKtp"
      :options="listKecamatan"
      :disabled="kecamatanDisabled"
      label="Kecamatan"
      v-model="legal_kecamatan"
      id="legal_kecamatan"
      value-field="kecamatan"
      text-field="kecamatan"
      :has-error="
        errorFields.includes('legal_kecamatan')
          ? errorMessages['legal_kecamatan']
          : false
      "
    />
    <select-field
      v-if="!alamatIsKtp"
      :options="listKelurahan"
      :disabled="kelurahanDisabled"
      label="Kelurahan / Desa"
      v-model="legal_kelurahan"
      id="legal_kelurahan"
      value-field="kelurahan"
      text-field="kelurahan"
      :has-error="
        errorFields.includes('legal_kelurahan')
          ? errorMessages['legal_kelurahan']
          : false
      "
    />
    <input-field
      v-if="!alamatIsKtp"
      id="legal_zip_code"
      v-model="legal_zip_code"
      label="Kode Pos"
      class="w-full"
      disabled
      :has-error="
        errorFields.includes('legal_zip_code')
          ? errorMessages['legal_zip_code']
          : false
      "
    />
  </div>
</template>

<script>
import TextArea from "./TextArea.vue";
import InputField from "./InputField.vue";
import SelectField from "./SelectField.vue";
import CheckBox from "./CheckBox.vue";
import InputDate from "./InputDate.vue";

import { store, mutations } from "../store";

export default {
  components: {
    InputField,
    SelectField,
    TextArea,
    CheckBox,
    InputDate
  },
  data() {
    return {
      unwatchProvince: null,
      unwatchCity: null,
      unwatchKecamatan: null,
      unwatchKelurahan: null,
      residence_address: null,
      residence_rt: null,
      residence_rw: null,
      listHouses: [],
      home_status: null,
      rent_since: null,
      rent_finish_date: null,
      alamatIsKtp: false,
      legal_address: null,
      legal_rt: null,
      legal_rw: null,
      legal_province: null,
      listKota: [],
      kotaDisabled: true,
      legal_city: null,
      listKecamatan: [],
      kecamatanDisabled: true,
      legal_kecamatan: null,
      listKelurahan: [],
      kelurahanDisabled: true,
      legal_kelurahan: null,
      legal_zip_code: null,
      errorFields: []
    };
  },
  computed: {
    fixedData() {
      return {
        residence_address: this.residence_address,
        residence_rt: this.residence_rt,
        residence_rw: this.residence_rw,
        residence_province: this.residence_province,
        residence_kelurahan: this.residence_kelurahan,
        residence_kecamatan: this.residence_kecamatan,
        residence_city: this.residence_city,
        residence_zip_code: this.residence_zip_code,
        home_status: this.home_status,
        rent_finish_date: this.rent_finish_date,
        stay_since_month: this.stay_since_month
          ? `${this.stay_since_month}`
          : null,
        stay_since_year: this.stay_since_year
          ? `${this.stay_since_year}`
          : null,
        legal_address: this.legal_address,
        legal_rt: this.legal_rt,
        legal_rw: this.legal_rw,
        legal_province: this.legal_province,
        legal_kelurahan: this.legal_kelurahan,
        legal_kecamatan: this.legal_kecamatan,
        legal_city: this.legal_city,
        legal_zip_code: this.legal_zip_code
      };
    },
    residence_province() {
      return store.userProvinsi;
    },
    residence_city() {
      return store.userCity;
    },
    residence_kecamatan() {
      return store.userKecamatan;
    },
    residence_kelurahan() {
      return store.userKelurahan;
    },
    residence_zip_code() {
      return store.userZip;
    },
    listProvinsi: {
      get() {
        return store.province;
      },
      set(val) {
        mutations.setProvince(val);
      }
    },
    stay_since_month() {
      // console.log(this.rent_since);
      return this.rent_since ? new Date(this.rent_since).getMonth() + 1 : null;
    },
    stay_since_year() {
      // console.log(this.rent_since);
      return this.rent_since ? new Date(this.rent_since).getFullYear() : null;
    }
  },
  watch: {
    alamatIsKtp(val) {
      if (val) {
        this.unwatchProvince();
        this.unwatchCity();
        this.unwatchKecamatan();
        this.unwatchKelurahan();
        this.$nextTick(() => {
          this.legal_address = this.residence_address;
          this.legal_rt = this.residence_rt;
          this.legal_rw = this.residence_rw;
          this.legal_province = this.residence_province;
          this.legal_city = this.residence_city;
          this.legal_kecamatan = this.residence_kecamatan;
          this.legal_kelurahan = this.residence_kelurahan;
          this.legal_zip_code = this.residence_zip_code;
        });
      } else {
        this.legal_address = null;
        this.legal_rt = null;
        this.legal_rw = null;
        this.legal_province = null;
        this.legal_city = null;
        this.legal_kecamatan = null;
        this.legal_kelurahan = null;
        this.legal_zip_code = null;
        this.$nextTick(() => {
          this.unwatchProvince = this.$watch(
            "legal_province",
            this.change_legal_province
          );
          this.unwatchCity = this.$watch("legal_city", this.change_legal_city);
          this.unwatchKecamatan = this.$watch(
            "legal_kecamatan",
            this.change_legal_kecamatan
          );
          this.unwatchKelurahan = this.$watch(
            "legal_kelurahan",
            this.change_legal_kelurahan
          );
        });
      }
    },
    home_status() {
      this.rent_finish_date = null;
    }
  },
  methods: {
    change_legal_province(val) {
      this.legal_city = null;
      if (val) {
        this.$http
          .get(`api/v1/master/city/${val}`, {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          })
          .then(res => res.json())
          .then(resKota => {
            this.listKota = resKota.data.items;
            this.kotaDisabled = false;
          });
      } else {
        this.listKota = [];
        this.kotaDisabled = true;
      }
    },
    change_legal_city(val) {
      this.legal_kecamatan = null;
      if (val) {
        this.$http
          .get(`api/v1/master/kecamatan/${this.legal_province}/${val}`, {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          })
          .then(res => res.json())
          .then(resKecamatan => {
            this.listKecamatan = resKecamatan.data.items;
            this.kecamatanDisabled = false;
          });
      } else {
        this.listKecamatan = [];
        this.kecamatanDisabled = true;
      }
    },
    change_legal_kecamatan(val) {
      this.legal_kelurahan = null;
      if (val) {
        this.$http
          .get(
            `api/v1/master/kelurahan/${this.legal_province}/${this.legal_city}/${val}`,
            {
              withCredentials: true,
              headers: {
                Authorization: `${this.$root.getParameterByName("token")}`
              }
            }
          )
          .then(res => res.json())
          .then(resKelurahan => {
            this.listKelurahan = resKelurahan.data.items;
            this.kelurahanDisabled = false;
          });
      } else {
        this.listKelurahan = [];
        this.kelurahanDisabled = true;
      }
    },
    change_legal_kelurahan(val) {
      this.legal_zip_code = null;
      if (val) {
        this.$http
          .get(
            `api/v1/master/zip/${this.legal_province}/${this.legal_city}/${this.legal_kecamatan}/${val}`,
            {
              withCredentials: true,
              headers: {
                Authorization: `${this.$root.getParameterByName("token")}`
              }
            }
          )
          .then(res => res.json())
          .then(resKelurahanDetail => {
            this.legal_zip_code = resKelurahanDetail.data.item[0].zipcode;
          });
      } else {
        this.legal_zip_code = null;
      }
    }
  },
  async mounted() {
    this.unwatchProvince = this.$watch(
      "legal_province",
      this.change_legal_province
    );
    this.unwatchCity = this.$watch("legal_city", this.change_legal_city);
    this.unwatchKecamatan = this.$watch(
      "legal_kecamatan",
      this.change_legal_kecamatan
    );
    this.unwatchKelurahan = this.$watch(
      "legal_kelurahan",
      this.change_legal_kelurahan
    );
    Promise.all([
      this.$http
        .get("api/v1/master/home-statuses", {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resHouses => ({
          key: "listHouses",
          data: resHouses.data.items
        }))
    ])
      .then(resAll => {
        resAll.map(res => {
          this[res.key] = res.data;
        });
      })
      .catch(resAll => resAll)
      .finally(() => {
        if (process.env.NODE_ENV === "development") {
          this.residence_address = "Jl. Hilih Kintil RESIDENCE No. 69";
          this.residence_rt = "005";
          this.residence_rw = "007";
          this.legal_address = "Jl. Hilih Kintil No. 69";
          this.legal_rt = "001";
          this.legal_rw = "003";
          this.home_status = "KS";
          // this.rent_since = "01-01-2019";
          this.alamatIsKtp = true;
        }
      });
  }
};
</script>
