<template>
  <div
    :class="{ 'hidden md:w-1/3 sticky md:py-6 px-8 md:px-0': !alwaysShow }"
    class="w-full z-0 invoice-container md:block"
  >
    <div
      :class="{
        'md:rounded-b-none md:rounded-t-none  md:rounded-l': !alwaysShow,
        'shadow-lg': sidebar
      }"
      class="text-grey-darker bg-gray-100 rounded overflow-hidden"
    >
      <div
        :class="{
          'bg-main text-kp-500 border-kp-500 justify-between': sidebar,
          'justify-center font-bold': !sidebar
        }"
        class="text-lg font-medium p-4 border-b tracking-wide flex  items-center"
      >
        Total Pinjaman
      </div>
      <div class="bg-grey-lightest flex flex-wrap text-gray-700 pb-2">
        <div
          v-for="n in invoices"
          :key="n.id"
          class="pt-2 px-0 md:px-6 w-full flex flex-wrap"
        >
          <header class="w-2/3">
            <div class="truncate">
              {{ n.category_id }} - {{ n.item_description }}
            </div>
          </header>
          <article class="w-1/3 text-right font-medium whitespace-pre">
            Rp {{ $root.convertRibu(n.otr * n.qty) }}
          </article>
          <footer class="w-full text-xs text-gray-600">
            <div>Harga: Rp {{ $root.convertRibu(n.otr) }}</div>
            <div>Jumlah: {{ $root.convertRibu(n.qty) }}</div>
          </footer>
        </div>
      </div>
      <div class="px-0 md:px-6 py-2 border-t">
        Subtotal
        <span class="font-medium float-right">
          Rp
          {{ $root.convertRibu(subTotal) }}
        </span>
      </div>
      <div
        v-if="!sidebar"
        class="border-t-2 border-dashed px-0 md:px-6 py-2 flex flex-wrap"
      >
        <header class="w-2/3">
          <div class="truncate font-medium">Total</div>
        </header>
        <article class="w-1/3 text-right font-medium text-kp-500">
          Rp
          <span v-if="calculateLoading">...</span>
          <span v-else>
            {{
              pembayaran.installment
                ? $root.convertRibu(pembayaran.installment * tenor)
                : 0
            }}
          </span>
        </article>
        <footer class="w-full text-gray-600">
          <div class="font-medium">
            Cicilan Tenor:
            <div
              class="inline-flex items-center shadow border rounded cursor-pointer bg-white relative"
            >
              <select
                class="py-0 pl-2 pr-5 flex-1 focus:outline-none bg-transparent z-10"
                v-model="tenor"
                @input="calculateTenor"
              >
                <option
                  v-for="(list, i) in listTenor"
                  :key="i"
                  class="text-gray-700"
                  :value="list.tenor"
                >
                  {{ list.tenor }}
                </option>
              </select>
              <svg
                class="absolute right-0 w-2 h-2 mr-2"
                ref="iconSelect"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 207 121.34"
                style="transform: rotate(180deg)"
              >
                <title>Icon</title>
                <path
                  d="M629.23,600.67a14.23,14.23,0,0,1-10.29-4.38L540,514.2l-78.93,82.09a14.28,14.28,0,1,1-20.58-19.79l89.22-92.79a14.28,14.28,0,0,1,20.58,0l89.22,92.79a14.28,14.28,0,0,1-10.29,24.17Z"
                  transform="translate(-436.5 -479.33)"
                />
              </svg>
            </div>
          </div>
          <div class="text-kp-500">
            {{ tenor }} bulan x Rp
            <span v-if="calculateLoading">...</span>
            <span v-else>
              {{
                pembayaran.installment
                  ? $root.convertRibu(pembayaran.installment)
                  : 0
              }}
            </span>
          </div>
        </footer>
      </div>
    </div>
  </div>
</template>

<script>
import { store, mutations } from "../store";

export default {
  props: {
    alwaysShow: {
      type: Boolean,
      default: false
    },
    sidebar: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      listTenor: [],
      subTotal: 0,
      calculateLoading: false
    };
  },
  computed: {
    invoices: {
      get() {
        return store.invoices;
      },
      set(val) {
        mutations.setInvoices(val);
      }
    },
    dp: {
      get() {
        return store.dp;
      },
      set(val) {
        mutations.setDp(val);
      }
    },
    prospect_id: {
      get() {
        return store.prospect_id;
      },
      set(val) {
        mutations.setProspectId(val);
      }
    },
    shipping_cost: {
      get() {
        return store.shipping_cost;
      },
      set(val) {
        mutations.setShippingCost(val);
      }
    },
    cat_id: {
      get() {
        return store.cat_id;
      },
      set(val) {
        mutations.setCatId(val);
      }
    },
    supplier_key: {
      get() {
        return store.supplier_key;
      },
      set(val) {
        mutations.setSupplierKey(val);
      }
    },
    tenor: {
      get() {
        return store.tenor;
      },
      set(val) {
        mutations.setTenor(val);
      }
    },
    pembayaran: {
      get() {
        return store.pembayaran;
      },
      set(val) {
        mutations.setPembayaran(val);
      }
    }
  },
  methods: {
    calculateTenor(e) {
      this.calculateLoading = true;
      const bln = e.target.value - 0;
      return this.$http
        .get(
          `api/v1/master/calculate/${this.supplier_key}/${this.cat_id}/${this.subTotal}/${bln}`,
          {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          }
        )
        .then(res => res.json())
        .catch(err => err)
        .then(data => {
          this.pembayaran = data.data.item;
          this.calculateLoading = false;
        });
    }
  },
  async mounted() {
    await this.$http
      .get("api/v1/master/asset", {
        withCredentials: true,
        headers: {
          Authorization: `${this.$root.getParameterByName("token")}`
        }
      })
      .then(res => res.json())
      .then(resAsset => {
        this.invoices = resAsset.data.items;
        this.supplier_key = resAsset.data.item.supplier_key;
        this.cat_id = resAsset.data.items[0].category_id;
        this.subTotal = this.invoices.reduce((tot, item) => {
          return tot + item.qty * item.otr;
        }, 0);
        this.dp = resAsset.data.item.dp;
        this.prospect_id = resAsset.data.item.prospect_id;
        this.shipping_cost = resAsset.data.item.shipping_cost;
      })
      .catch(err => err);
    Promise.all([
      this.$http
        .get(`api/v1/master/tenor/${this.supplier_key}/${this.cat_id}`, {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resTenor => ({
          key: "listTenor",
          data: resTenor.data.items
        })),
      this.$http
        .get(
          `api/v1/master/calculate/${this.supplier_key}/${this.cat_id}/${this.subTotal}/${this.tenor}`,
          {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          }
        )
        .then(res => res.json())
        .then(resTenor => ({
          key: "pembayaran",
          data: resTenor.data.item
        }))
    ]).then(resAll => {
      resAll.map(res => {
        this[res.key] = res.data;
      });
    });
  }
};
</script>
