<script>
import SelectField from "./SelectField.vue";
import { store, mutations } from "../store";

export default {
  components: {
    SelectField
  },
  data() {
    return {
      masterLoading: true,
      branchNotFound: false,
      listKota: [],
      kotaDisabled: true,
      listKecamatan: [],
      kecamatanDisabled: true,
      listKelurahan: [],
      kelurahanDisabled: true,
      noToken: false
    };
  },
  computed: {
    listProvinsi: {
      get() {
        return store.province;
      },
      set(val) {
        mutations.setProvince(val);
      }
    },
    userProvinsi: {
      get() {
        return store.userProvinsi;
      },
      set(val) {
        mutations.setUserProvinsi(val);
      }
    },
    userCity: {
      get() {
        return store.userCity;
      },
      set(val) {
        mutations.setUserCity(val);
      }
    },
    userKecamatan: {
      get() {
        return store.userKecamatan;
      },
      set(val) {
        mutations.setUserKecamatan(val);
      }
    },
    userKelurahan: {
      get() {
        return store.userKelurahan;
      },
      set(val) {
        mutations.setUserKelurahan(val);
      }
    }
  },
  watch: {
    userProvinsi(val) {
      this.userCity = null;
      this.$http
        .get(`api/v1/master/city/${val}`, {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resKota => {
          this.listKota = resKota.data.items;
          this.kotaDisabled = false;
        });
    },
    userCity(val) {
      this.userKecamatan = null;
      if (val) {
        this.$http
          .get(`api/v1/master/kecamatan/${this.userProvinsi}/${val}`, {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          })
          .then(res => res.json())
          .then(resKecamatan => {
            this.listKecamatan = resKecamatan.data.items;
            this.kecamatanDisabled = false;
          });
      } else {
        this.listKecamatan = [];
        this.kecamatanDisabled = true;
      }
    },
    userKecamatan(val) {
      this.userKelurahan = null;
      if (val) {
        this.$http
          .get(
            `api/v1/master/kelurahan/${this.userProvinsi}/${this.userCity}/${val}`,
            {
              withCredentials: true,
              headers: {
                Authorization: `${this.$root.getParameterByName("token")}`
              }
            }
          )
          .then(res => res.json())
          .then(resKecamatan => {
            this.listKelurahan = resKecamatan.data.items;
            this.kelurahanDisabled = false;
          });
      } else {
        this.listKelurahan = [];
        this.kelurahanDisabled = true;
      }
    }
  },
  mounted() {
    this.$http
      .get("api/v1/master/province", {
        withCredentials: true,
        headers: {
          Authorization: `${this.$root.getParameterByName("token")}`
        }
      })
      .then(res => res.json())
      .then(resProvince => {
        this.listProvinsi = resProvince.data.items;
        this.masterLoading = false;
      })
      .catch(() => {
        this.branchNotFound = true;
        this.noToken = true;
        this.masterLoading = false;
      });
  },
  methods: {
    async checkBranch() {
      const hasBranch = await this.$http
        .get(
          `api/v1/master/zip/${this.userProvinsi}/${this.userCity}/${this.userKecamatan}/${this.userKelurahan}`,
          {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          }
        )
        .then(res => res.json())
        .then(resBranch => {
          if (
            resBranch.data.item[0].branch_id &&
            resBranch.data.item[0].branch_id !== ""
          ) {
            mutations.setUserZip(resBranch.data.item[0].zipcode);
            return true;
          } else {
            return false;
          }
        })
        .catch(() => false);
      if (hasBranch) {
        mutations.setHasBranch(true);
      } else {
        this.branchNotFound = true;
      }
    }
  }
};
</script>

