<template>
  <div class="mb-4">
    <label class="block text-grey-darker text-sm font-bold mb-2" :for="idna">
      {{ label }}
    </label>
    <div
      class="flex shadow appearance-none border rounded h-12 w-full items-center"
      :class="{ 'border-kp-500': isError }"
    >
      <span class="px-2">Rp.</span>
      <input
        class="h-full w-full py-2 px-3 text-grey-darker leading-tight focus:outline-none"
        :id="idna"
        :placeholder="label"
        v-model="tempValue"
        @input="formatRb"
      />
      <span class="px-3">.00</span>
    </div>
    <ul v-if="isError" class="list-disc list-inside text-kp-500 mt-1">
      <li v-for="(err, i) in errorList" :key="i">
        {{ err.replace(new RegExp(".+(?= field)"), label) }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: [String, Number],
      default: ""
    },
    label: {
      type: String,
      default: ""
    },
    id: {
      type: String,
      default: null
    },
    hasError: {
      type: [Array, Boolean],
      default() {
        return null;
      }
    }
  },
  watch: {
    value(val) {
      if (this.errorList.length > 0) {
        this.errorList.length = 0;
      }
      this.tempValue = val
        ? val
            .toString()
            .replace(/[^\d]+/g, "")
            .replace(/\B(?=(?:\d{3})+(?!\d))/g, ".")
        : null;
    },
    hasError(val) {
      if (val.length > 0) {
        this.errorList = val;
        this.isError = true;
      } else {
        this.isError = false;
      }
    }
  },
  data() {
    return {
      tempValue: null,
      isError: this.hasError,
      errorList: []
    };
  },
  computed: {
    idna() {
      return this.id || this.label.replace(/ /, "");
    }
  },
  methods: {
    formatRb(e) {
      if (this.isError) {
        this.isError = false;
      }
      let val = e.target.value;
      val = parseInt(val.split(".").join(""));
      this.tempValue = isNaN(val)
        ? ""
        : val
            // eslint-disable-next-line indent
            .toString()
            // eslint-disable-next-line indent
            .replace(/[^\d]+/g, "")
            // eslint-disable-next-line indent
            .replace(/\B(?=(?:\d{3})+(?!\d))/g, ".");
      this.$emit("input", `${val}`);
    }
  }
};
</script>
