<template>
  <div
    class="block sm:flex md:block lg:flex items-center justify-center flex-col px-6 py-4"
  >
    <div class="flex justify-center items-center mb-4 flex-wrap">
      <span class="text-lg font-bold">Data Pribadi</span>
      <span class="w-full text-center">
        Lengkapi Data Pribadi anda.
      </span>
    </div>
    <input-field
      label="Nomor KTP"
      class="w-full"
      v-model="id_number"
      id="id_number"
      :has-error="
        errorFields.includes('id_number') ? errorMessages['id_number'] : false
      "
      no-special
      numeric
      max="16"
    />
    <input-field
      label="Nama Lengkap"
      class="w-full"
      v-model="full_name"
      id="full_name"
      :has-error="
        errorFields.includes('full_name') ? errorMessages['full_name'] : false
      "
      no-special
      max="100"
    />
    <input-field
      label="Nama Sesuai KTP"
      class="w-full"
      v-model="legal_name"
      id="legal_name"
      :has-error="
        errorFields.includes('legal_name') ? errorMessages['legal_name'] : false
      "
      no-special
      max="100"
    />
    <input-field
      label="Tempat Lahir"
      class="w-full"
      v-model="birth_place"
      id="birth_place"
      no-special
      :has-error="
        errorFields.includes('birth_place')
          ? errorMessages['birth_place']
          : false
      "
    />
    <input-date
      label="Tanggal Lahir"
      class="w-full"
      full-date
      v-model="birth_date"
      id="birth_date"
      :has-error="
        errorFields.includes('birth_date') ? errorMessages['birth_date'] : false
      "
      :disabled-dates="{ from: new Date() }"
    />
    <select-field
      :options="listKelamin"
      label="Jenis Kelamin"
      v-model="gender"
      id="gender"
      :has-error="
        errorFields.includes('gender') ? errorMessages['gender'] : false
      "
    />
    <input-field
      label="Nama Gadis Ibu Kandung"
      class="w-full"
      v-model="surgate_mother_name"
      id="surgate_mother_name"
      no-special
      :has-error="
        errorFields.includes('surgate_mother_name')
          ? errorMessages['surgate_mother_name']
          : false
      "
    />
    <select-field
      :options="listAgama"
      label="Agama"
      v-model="religion"
      id="religion"
      :has-error="
        errorFields.includes('religion') ? errorMessages['religion'] : false
      "
      value-field="id_confins"
    />
    <select-field
      :options="listPernikahan"
      label="Status Pernikahan"
      v-model="marital_status"
      id="marital_status"
      :has-error="
        errorFields.includes('marital_status')
          ? errorMessages['marital_status']
          : false
      "
      value-field="id_confins"
    />
    <input-field
      label="Jumlah Tanggungan"
      class="w-full"
      v-model="num_of_dependence"
      id="num_of_dependence"
      no-special
      numeric
      :has-error="
        errorFields.includes('num_of_dependence')
          ? errorMessages['num_of_dependence']
          : false
      "
    />
    <select-field
      :options="listEdu"
      label="Pendidikan Terakhir"
      v-model="education"
      id="education"
      :has-error="
        errorFields.includes('education') ? errorMessages['education'] : false
      "
      value-field="id_confins"
    />
  </div>
</template>

<script>
import InputField from "./InputField.vue";
import SelectField from "./SelectField.vue";
import InputDate from "./InputDate.vue";

export default {
  components: {
    InputField,
    SelectField,
    InputDate
  },
  data() {
    return {
      id_number: null,
      full_name: null,
      legal_name: null,
      birth_place: null,
      birth_date: null,
      surgate_mother_name: null,
      listKelamin: [
        { id: "M", description: "Laki-laki" },
        { id: "F", description: "Perempuan" }
      ],
      gender: null,
      listAgama: [],
      religion: null,
      listPernikahan: [],
      marital_status: null,
      num_of_dependence: null,
      listEdu: [],
      education: null,
      errorFields: [],
      errorMessages: []
    };
  },
  computed: {
    fixedData() {
      return {
        id_number: this.id_number,
        full_name: this.full_name,
        legal_name: this.legal_name,
        birth_place: this.birth_place,
        birth_date: this.birth_date,
        surgate_mother_name: this.surgate_mother_name,
        gender: this.gender,
        religion: this.religion,
        marital_status: this.marital_status,
        num_of_dependence: this.num_of_dependence,
        education: this.education
      };
    }
  },
  mounted() {
    Promise.all([
      this.$http
        .get("api/v1/master/religions", {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resAgama => ({
          key: "listAgama",
          data: resAgama.data.items
        })),
      this.$http
        .get("api/v1/master/educations", {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resEdu => ({
          key: "listEdu",
          data: resEdu.data.items
        })),
      this.$http
        .get("api/v1/master/marital-statuses", {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resPernikahan => ({
          key: "listPernikahan",
          data: resPernikahan.data.items
        }))
    ])
      .then(resAll => {
        resAll.map(res => {
          this[res.key] = res.data;
        });
        // console.log("COMPLETED\n", resAll);
        if (process.env.NODE_ENV === "development") {
          this.$nextTick(() => {
            this.id_number = "1293812983";
            this.full_name = "huzairuje";
            this.legal_name = "muhammad huzai";
            this.birth_place = "Padang";
            this.birth_date = "1993-05-29";
            this.surgate_mother_name = "El";
            this.gender = "M";
            this.num_of_dependence = "2";
            this.religion = "1";
            this.marital_status = "S";
            this.education = "D3";
          });
        }
      })
      .catch(resAll => resAll);
  }
};
</script>
