<template>
  <div
    class="block sm:flex md:block lg:flex items-center justify-center flex-col px-6 py-4"
  >
    <div class="flex justify-center items-center mb-4 flex-wrap">
      <span class="text-lg font-bold">Data Pekerjaan</span>
      <span class="w-full text-center">
        Masukkan Data Pekerjaan Anda.
      </span>
    </div>
    <input-field
      label="Nama Perusahaan"
      class="w-full"
      v-model="company_name"
      id="company_name"
      :has-error="
        errorFields.includes('company_name')
          ? errorMessages['company_name']
          : false
      "
    />
    <select-field
      :options="listIndustri"
      id="industry_type_id"
      v-model="industry_type_id"
      label="Industri Perusahaan"
      :has-error="
        errorFields.includes('industry_type_id')
          ? errorMessages['industry_type_id']
          : false
      "
    />
    <select-field
      :options="listProfesi"
      v-model="profession_id"
      label="Profesi"
      id="profession_id"
      value-field="id_confins"
      :has-error="
        errorFields.includes('profession_id')
          ? errorMessages['profession_id']
          : false
      "
    />
    <select-field
      :options="listPekerjaan"
      v-model="job_type"
      label="Jenis Pekerjaan"
      :disabled="profession_id === null"
      value-field="job_type_id"
      id="job_type"
      :has-error="
        errorFields.includes('job_type') ? errorMessages['job_type'] : false
      "
    />
    <select-field
      :options="listJabatan"
      id="job_position"
      :disabled="job_type === null"
      v-model="job_position"
      value-field="job_position_id"
      label="Jabatan"
      :has-error="
        errorFields.includes('job_position')
          ? errorMessages['job_position']
          : false
      "
    />
    <input-date
      label="Bekerja Sejak"
      class="w-full"
      v-model="employment_since"
      id="employment_since"
      :has-error="
        errorFields.includes('employment_since')
          ? errorMessages['employment_since']
          : false
      "
      :open-date="dateNa"
      :disabled-dates="{ from: dateNa }"
    />
    <input-price
      label="Penghasilan"
      class="w-full"
      v-model="monthly_fixed_income"
      id="monthly_fixed_income"
      :has-error="
        errorFields.includes('monthly_fixed_income')
          ? errorMessages['monthly_fixed_income']
          : false
      "
    />
    <input-price
      label="Penghasilan lainnya"
      class="w-full"
      v-model="monthly_variable_income"
      id="monthly_variable_income"
      :has-error="
        errorFields.includes('monthly_variable_income')
          ? errorMessages['monthly_variable_income']
          : false
      "
    />
    <input-price
      label="Biaya Hidup per bulan"
      class="w-full"
      v-model="living_cost_amount"
      id="living_cost_amount"
      :has-error="
        errorFields.includes('living_cost_amount')
          ? errorMessages['living_cost_amount']
          : false
      "
    />
    <input-date
      v-if="profession_id === 'WRST'"
      label="Omset bulan & tahun"
      class="w-full"
      v-model="monthly_omset_1"
      :disabled-dates="{ from: dateNa }"
      :open-date="dateNa"
      id="monthly_omset_1"
      :has-error="
        errorFields.includes('monthly_omset_1')
          ? errorMessages['monthly_omset_1']
          : false
      "
    />
    <input-price
      v-if="profession_id === 'WRST'"
      label="Omset per bulan"
      class="w-full"
      v-model="monthly_omset_1_omset"
      id="monthly_omset_1_omset"
      :has-error="
        errorFields.includes('monthly_omset_1_omset')
          ? errorMessages['monthly_omset_1_omset']
          : false
      "
    />
    <input-date
      v-if="profession_id === 'WRST'"
      label="Omset bulan & tahun"
      class="w-full"
      v-model="monthly_omset_2"
      :disabled-dates="{ from: dateNa }"
      :open-date="dateNa"
      id="monthly_omset_2"
      :has-error="
        errorFields.includes('monthly_omset_2')
          ? errorMessages['monthly_omset_2']
          : false
      "
    />
    <input-price
      v-if="profession_id === 'WRST'"
      label="Omset per bulan"
      class="w-full"
      v-model="monthly_omset_2_omset"
      id="monthly_omset_2_omset"
      :has-error="
        errorFields.includes('monthly_omset_2_omset')
          ? errorMessages['monthly_omset_2_omset']
          : false
      "
    />
    <input-date
      v-if="profession_id === 'WRST'"
      label="Omset bulan & tahun"
      class="w-full"
      v-model="monthly_omset_3"
      :disabled-dates="{ from: dateNa }"
      :open-date="dateNa"
      id="monthly_omset_3"
      :has-error="
        errorFields.includes('monthly_omset_3')
          ? errorMessages['monthly_omset_3']
          : false
      "
    />
    <input-price
      v-if="profession_id === 'WRST'"
      label="Omset per bulan"
      class="w-full"
      v-model="monthly_omset_3_omset"
      id="monthly_omset_3_omset"
      :has-error="
        errorFields.includes('monthly_omset_3_omset')
          ? errorMessages['monthly_omset_3_omset']
          : false
      "
    />
  </div>
</template>

<script>
import InputField from "./InputField.vue";
import InputPrice from "./InputPrice.vue";
import SelectField from "./SelectField.vue";
import InputDate from "./InputDate.vue";

export default {
  components: {
    InputField,
    SelectField,
    InputPrice,
    InputDate
  },
  data() {
    return {
      unwatch_profession_id: null,
      unwatch_job_type: null,
      company_name: null,
      industry_type_id: null,
      profession_id: null,
      job_type: null,
      job_position: null,
      employment_since: null,
      monthly_fixed_income: null,
      monthly_variable_income: null,
      living_cost_amount: null,
      monthly_omset_1: null,
      monthly_omset_1_omset: null,
      monthly_omset_2: null,
      monthly_omset_2_omset: null,
      monthly_omset_3: null,
      monthly_omset_3_omset: null,
      listIndustri: [],
      listProfesi: [],
      listPekerjaan: [],
      listJabatan: [],
      errorFields: []
    };
  },
  computed: {
    employment_since_month() {
      return this.employment_since
        ? `${new Date(this.employment_since).getMonth() + 1}`
        : null;
    },
    employment_since_year() {
      return this.employment_since
        ? `${new Date(this.employment_since).getFullYear()}`
        : null;
    },
    monthly_omset_1_month() {
      return this.monthly_omset_1
        ? `${new Date(this.monthly_omset_1).getMonth() + 1}`
        : null;
    },
    monthly_omset_1_year() {
      return this.monthly_omset_1
        ? `${new Date(this.monthly_omset_1).getFullYear()}`
        : null;
    },
    monthly_omset_2_month() {
      return this.monthly_omset_2
        ? `${new Date(this.monthly_omset_2).getMonth() + 1}`
        : null;
    },
    monthly_omset_2_year() {
      return this.monthly_omset_2
        ? `${new Date(this.monthly_omset_2).getFullYear()}`
        : null;
    },
    monthly_omset_3_month() {
      return this.monthly_omset_3
        ? `${new Date(this.monthly_omset_3).getMonth() + 1}`
        : null;
    },
    monthly_omset_3_year() {
      return this.monthly_omset_3
        ? `${new Date(this.monthly_omset_3).getFullYear()}`
        : null;
    },
    dateNa() {
      const date = new Date();
      return new Date(date.setMonth(date.getMonth() - 1));
    },
    fixedData() {
      return {
        company_name: this.company_name,
        industry_type_id: this.industry_type_id,
        profession_id: this.profession_id,
        job_type: this.job_type,
        job_position: this.job_position,
        employment_since_month: this.employment_since_month,
        employment_since_year: this.employment_since_year,
        monthly_fixed_income: this.monthly_fixed_income,
        monthly_variable_income: this.monthly_variable_income,
        living_cost_amount: this.living_cost_amount,
        monthly_omset_1_month: this.monthly_omset_1_month,
        monthly_omset_1_year: this.monthly_omset_1_year,
        monthly_omset_1_omset: this.monthly_omset_1_omset,
        monthly_omset_2_month: this.monthly_omset_2_month,
        monthly_omset_2_year: this.monthly_omset_2_year,
        monthly_omset_2_omset: this.monthly_omset_2_omset,
        monthly_omset_3_month: this.monthly_omset_3_month,
        monthly_omset_3_year: this.monthly_omset_3_year,
        monthly_omset_3_omset: this.monthly_omset_3_omset
      };
    }
  },
  methods: {
    change_profession_id(val) {
      this.job_type = null;
      this.listJabatan = [];
      this.listPekerjaan = [];
      this.monthly_omset_1 = null;
      this.monthly_omset_1_omset = null;
      this.monthly_omset_2 = null;
      this.monthly_omset_2_omset = null;
      this.monthly_omset_3 = null;
      this.monthly_omset_3_omset = null;
      this.$http
        .get(`api/v1/master/job-types/find-by-id/${val}`, {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resPekerjaan => {
          this.listPekerjaan = resPekerjaan.data.items;
        });
    },
    change_job_type(val) {
      this.job_position = null;
      this.$http
        .get(`api/v1/master/job-position/job-type-id/${val}`, {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resJabatan => {
          this.listJabatan = resJabatan.data.items;
        });
    }
  },
  mounted() {
    this.unwatch_profession_id = this.$watch(
      "profession_id",
      this.change_profession_id
    );
    this.unwatch_job_type = this.$watch("job_type", this.change_job_type);
    Promise.all([
      this.$http
        .get("api/v1/master/industry-types", {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resIndustri => ({
          key: "listIndustri",
          data: resIndustri.data.items
        })),
      this.$http
        .get("api/v1/master/profession", {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resProfesi => ({
          key: "listProfesi",
          data: resProfesi.data.items
        }))
    ])
      .then(resAll => {
        resAll.map(res => {
          this[res.key] = res.data;
        });
      })
      .catch(resAll => resAll)
      .finally(() => {
        if (process.env.NODE_ENV === "development") {
          this.company_name = "Finansia Multi Finance";
          // this.employment_since = "2016-01-01";
          this.monthly_fixed_income = "2000000";
          this.monthly_variable_income = "1000000";
          this.living_cost_amount = "500000";
          // this.monthly_omset_1 = "2019-10-19";
          // this.monthly_omset_1_omset = "2000000";
          // this.monthly_omset_2 = "2019-11-19";
          // this.monthly_omset_2_omset = "2500000";
          // this.monthly_omset_3 = "2019-12-19";
          // this.monthly_omset_3_omset = "2600000";
        }
      });
  }
};
</script>
