<template>
  <div class="w-full">
    <div
      class="block sm:flex md:block lg:flex items-center justify-center flex-col px-6 py-4"
    >
      <div class="flex justify-center items-center mb-4 flex-wrap">
        <span class="text-lg font-bold">Kontak Darurat</span>
        <span class="w-full text-center">
          Keluarga dekat tidak serumah yang dapat dihubungi dalam keadaan
          darurat.
        </span>
      </div>
      <input-field
        label="Nama Lengkap"
        class="w-full"
        id="emergency_contact_name"
        v-model="emergency_contact_name"
        no-special
        max="100"
        :has-error="
          errorFields.includes('emergency_contact_name')
            ? errorMessages['emergency_contact_name']
            : false
        "
      />
      <select-field
        :options="listFamily"
        label="Hubungan Kerabat"
        v-model="emergency_contact_relationship"
        value-field="id_confins"
        id="emergency_contact_relationship"
        :has-error="
          errorFields.includes('emergency_contact_relationship')
            ? errorMessages['emergency_contact_relationship']
            : false
        "
      />
      <text-area
        label="Alamat Tinggal Sekarang (Lengkap)"
        tag="textarea"
        v-model="emergency_contact_address"
        id="emergency_contact_address"
        :has-error="
          errorFields.includes('emergency_contact_address')
            ? errorMessages['emergency_contact_address']
            : false
        "
      />
      <div class="w-full flex">
        <input-field
          label="RT"
          class="w-1/2 inline-block mr-4"
          v-model="emergency_contact_rt"
          id="emergency_contact_rt"
          max="3"
          numeric
          :has-error="
            errorFields.includes('emergency_contact_rt')
              ? errorMessages['emergency_contact_rt']
              : false
          "
        />
        <input-field
          label="RW"
          class="w-1/2 inline-block"
          v-model="emergency_contact_rw"
          id="emergency_contact_rw"
          max="3"
          numeric
          :has-error="
            errorFields.includes('emergency_contact_rw')
              ? errorMessages['emergency_contact_rw']
              : false
          "
        />
      </div>
      <select-field
        :options="listProvinsi"
        label="Provinsi"
        v-model="emergency_contact_province"
        id="emergency_contact_province"
        :has-error="
          errorFields.includes('emergency_contact_province')
            ? errorMessages['emergency_contact_province']
            : false
        "
        value-field="province"
        text-field="province"
      />
      <select-field
        :options="listKota"
        :disabled="kotaDisabled"
        label="Kota / Kabupaten"
        v-model="emergency_contact_city"
        id="emergency_contact_city"
        value-field="city"
        text-field="city"
        :has-error="
          errorFields.includes('emergency_contact_city')
            ? errorMessages['emergency_contact_city']
            : false
        "
      />
      <select-field
        :options="listKecamatan"
        :disabled="kecamatanDisabled"
        label="Kecamatan"
        v-model="emergency_contact_kecamatan"
        id="emergency_contact_kecamatan"
        value-field="kecamatan"
        text-field="kecamatan"
        :has-error="
          errorFields.includes('emergency_contact_kecamatan')
            ? errorMessages['emergency_contact_kecamatan']
            : false
        "
      />
      <select-field
        :options="listKelurahan"
        :disabled="kelurahanDisabled"
        label="Kelurahan / Desa"
        v-model="emergency_contact_kelurahan"
        id="emergency_contact_kelurahan"
        value-field="kelurahan"
        text-field="kelurahan"
        :has-error="
          errorFields.includes('emergency_contact_kelurahan')
            ? errorMessages['emergency_contact_kelurahan']
            : false
        "
      />
      <input-field
        label="Kode Pos"
        class="w-full"
        v-model="emergency_contact_zip_code"
        disabled
        :has-error="
          errorFields.includes('emergency_contact_zip_code')
            ? errorMessages['emergency_contact_zip_code']
            : false
        "
      />
      <div class="flex w-full">
        <input-field
          label="Kode Area"
          class="w-32 mr-4"
          id="emergency_contact_home_phone_area_1"
          numeric
          max="4"
          v-model="emergency_contact_home_phone_area_1"
          :has-error="
            errorFields.includes('emergency_contact_home_phone_area_1')
              ? errorMessages['emergency_contact_home_phone_area_1']
              : false
          "
        />
        <input-field
          label="Telepon Rumah"
          class="w-full"
          id="emergency_contact_home_phone_1"
          numeric
          max="10"
          v-model="emergency_contact_home_phone_1"
          :has-error="
            errorFields.includes('emergency_contact_home_phone_1')
              ? errorMessages['emergency_contact_home_phone_1']
              : false
          "
        />
      </div>
      <div class="flex w-full">
        <input-field
          label="Kode Area"
          class="w-32 mr-4"
          id="emergency_contact_office_phone_area_1"
          numeric
          max="4"
          v-model="emergency_contact_office_phone_area_1"
          :has-error="
            errorFields.includes('emergency_contact_office_phone_area_1')
              ? errorMessages['emergency_contact_office_phone_area_1']
              : false
          "
        />
        <input-field
          label="Telepon Rumah"
          class="w-full"
          id="emergency_contact_office_phone_1"
          max="10"
          numeric
          v-model="emergency_contact_office_phone_1"
          :has-error="
            errorFields.includes('emergency_contact_office_phone_1')
              ? errorMessages['emergency_contact_office_phone_1']
              : false
          "
        />
      </div>
      <input-field
        label="Nomor Handphone"
        class="w-full"
        numeric
        max="20"
        v-model="emergency_contact_mobile_phone"
        id="emergency_contact_mobile_phone"
        :has-error="
          errorFields.includes('emergency_contact_mobile_phone')
            ? errorMessages['emergency_contact_mobile_phone']
            : false
        "
      />
    </div>
  </div>
</template>

<script>
import InputField from "./InputField.vue";
import SelectField from "./SelectField.vue";
import TextArea from "./TextArea.vue";

import { store } from "../store";

export default {
  components: {
    InputField,
    SelectField,
    TextArea
  },
  data() {
    return {
      emergency_contact_name: null,
      emergency_contact_relationship: null,
      emergency_contact_address: null,
      emergency_contact_rt: null,
      emergency_contact_rw: null,
      emergency_contact_province: null,
      emergency_contact_city: null,
      emergency_contact_kecamatan: null,
      emergency_contact_kelurahan: null,
      emergency_contact_zip_code: null,
      emergency_contact_home_phone_area_1: null,
      emergency_contact_home_phone_1: null,
      emergency_contact_office_phone_area_1: null,
      emergency_contact_office_phone_1: null,
      emergency_contact_mobile_phone: null,
      listKota: [],
      kotaDisabled: true,
      listKecamatan: [],
      kecamatanDisabled: true,
      listKelurahan: [],
      kelurahanDisabled: true,
      listFamily: [],
      unwatchProvince: null,
      unwatchCity: null,
      unwatchKecamatan: null,
      unwatchKelurahan: null,
      errorFields: []
    };
  },
  computed: {
    listProvinsi() {
      return store.province;
    },
    fixedData() {
      return {
        emergency_contact_name: this.emergency_contact_name,
        emergency_contact_relationship: this.emergency_contact_relationship,
        emergency_contact_address: this.emergency_contact_address,
        emergency_contact_rt: this.emergency_contact_rt,
        emergency_contact_rw: this.emergency_contact_rw,
        emergency_contact_province: this.emergency_contact_province,
        emergency_contact_city: this.emergency_contact_city,
        emergency_contact_kecamatan: this.emergency_contact_kecamatan,
        emergency_contact_kelurahan: this.emergency_contact_kelurahan,
        emergency_contact_zip_code: this.emergency_contact_zip_code,
        emergency_contact_home_phone_area_1: this
          .emergency_contact_home_phone_area_1,
        emergency_contact_home_phone_1: this.emergency_contact_home_phone_1,
        emergency_contact_office_phone_area_1: this
          .emergency_contact_office_phone_area_1,
        emergency_contact_office_phone_1: this.emergency_contact_office_phone_1,
        emergency_contact_mobile_phone: this.emergency_contact_mobile_phone
      };
    }
  },
  methods: {
    change_emergency_contact_province(val) {
      this.emergency_contact_city = null;
      if (val) {
        this.$http
          .get(`api/v1/master/city/${val}`, {
            withCredentials: true,
            headers: {
              Authorization: `${this.$root.getParameterByName("token")}`
            }
          })
          .then(res => res.json())
          .then(resKota => {
            this.listKota = resKota.data.items;
            this.kotaDisabled = false;
          });
      } else {
        this.listKota = [];
        this.kotaDisabled = true;
      }
    },
    change_emergency_contact_city(val) {
      this.emergency_contact_kecamatan = null;
      if (val) {
        this.$http
          .get(
            `api/v1/master/kecamatan/${this.emergency_contact_province}/${val}`,
            {
              withCredentials: true,
              headers: {
                Authorization: `${this.$root.getParameterByName("token")}`
              }
            }
          )
          .then(res => res.json())
          .then(resKecamatan => {
            this.listKecamatan = resKecamatan.data.items;
            this.kecamatanDisabled = false;
          });
      } else {
        this.listKecamatan = [];
        this.kecamatanDisabled = true;
      }
    },
    change_emergency_contact_kecamatan(val) {
      this.emergency_contact_kelurahan = null;
      if (val) {
        this.$http
          .get(
            `api/v1/master/kelurahan/${this.emergency_contact_province}/${this.emergency_contact_city}/${val}`,
            {
              withCredentials: true,
              headers: {
                Authorization: `${this.$root.getParameterByName("token")}`
              }
            }
          )
          .then(res => res.json())
          .then(resKecamatan => {
            this.listKelurahan = resKecamatan.data.items;
            this.kelurahanDisabled = false;
          });
      } else {
        this.listKelurahan = [];
        this.kelurahanDisabled = true;
      }
    },
    change_emergency_contact_kelurahan(val) {
      this.emergency_contact_zip_code = null;
      if (val) {
        this.$http
          .get(
            `api/v1/master/zip/${this.emergency_contact_province}/${this.emergency_contact_city}/${this.emergency_contact_kecamatan}/${val}`,
            {
              withCredentials: true,
              headers: {
                Authorization: `${this.$root.getParameterByName("token")}`
              }
            }
          )
          .then(res => res.json())
          .then(resKelurahan => {
            this.emergency_contact_zip_code = resKelurahan.data.item[0].zipcode;
          });
      } else {
        this.emergency_contact_zip_code = null;
      }
    }
  },
  mounted() {
    this.unwatchProvince = this.$watch(
      "emergency_contact_province",
      this.change_emergency_contact_province
    );
    this.unwatchCity = this.$watch(
      "emergency_contact_city",
      this.change_emergency_contact_city
    );
    this.unwatchKecamatan = this.$watch(
      "emergency_contact_kecamatan",
      this.change_emergency_contact_kecamatan
    );
    this.unwatchKelurahan = this.$watch(
      "emergency_contact_kelurahan",
      this.change_emergency_contact_kelurahan
    );
    Promise.all([
      this.$http
        .get("api/v1/master/family", {
          withCredentials: true,
          headers: {
            Authorization: `${this.$root.getParameterByName("token")}`
          }
        })
        .then(res => res.json())
        .then(resFamily => ({
          key: "listFamily",
          data: resFamily.data.items
        }))
    ])
      .then(resAll => {
        resAll.map(res => {
          this[res.key] = res.data;
        });
      })
      .catch(resAll => resAll)
      .finally(() => {
        if (process.env.NODE_ENV === "development") {
          this.emergency_contact_name = "Yazid Nurfadil";
          this.emergency_contact_relationship = "BS";
          this.emergency_contact_address = "Jl. Hilih Khintil No.21";
          this.emergency_contact_rt = "05";
          this.emergency_contact_rw = "07";
          this.emergency_contact_province = "JAWA BARAT";
          this.emergency_contact_home_phone_area_1 = "022";
          this.emergency_contact_home_phone_1 = "1234556789";
          this.emergency_contact_office_phone_area_1 = "022";
          this.emergency_contact_office_phone_1 = "1234556789";
          this.emergency_contact_mobile_phone = "08997165729";
        }
      });
  }
};
</script>
