import Vue from "vue";
import App from "./App.vue";
import "@/assets/css/tailwind.css";
import VueSlideTab from "vue-slide-tab";
import ky from "ky";
// Main Components
import DataPribadi from "./components/DataPribadi.vue";
import KontakDarurat from "./components/KontakDarurat.vue";
import DataPekerjaan from "./components/Pekerjaan.vue";
import Lampiran from "./components/Lampiran.vue";
import VideoPopup from "./components/VideoPopup.vue";
import Review from "./components/Review.vue";

Vue.config.productionTip = false;
Vue.prototype.$http = ky.create({
  prefixUrl:
    process.env.NODE_ENV === "production"
      ? "https://api.kreditplus.com/"
      : "http://202.147.198.221:1733/",
  timeout: false
});
Vue.component("VideoPopup", VideoPopup);
Vue.component("Tabs", VueSlideTab.component);
Vue.component("DataPribadi", DataPribadi);
Vue.component("KontakDarurat", KontakDarurat);
Vue.component("DataPekerjaan", DataPekerjaan);
Vue.component("Lampiran", Lampiran);
Vue.component("Review", Review);
new Vue({
  render: h => h(App),
  data() {
    return {
      cur: 0,
      tabs: [
        { component: "Data Pribadi", componentId: "DataPribadi" },
        { component: "Kontak Darurat", componentId: "KontakDarurat" },
        { component: "Data Pekerjaan", componentId: "DataPekerjaan" },
        { component: "Lampiran", componentId: "Lampiran" },
        { component: "Review", componentId: "Review" }
      ],
      ktp: null,
      selfie: null,
      isVideo: null,
      getSnap: null,
      stepLoading: false
    };
  },
  methods: {
    async showVideo(snap) {
      this.isVideo = true;
      this.getSnap = snap;
      // document.body.insertBefore(this.$el, document.body.firstChild);
    },
    getParameterByName(name, url) {
      if (!url) url = window.location.href;
      // eslint-disable-next-line no-useless-escape
      name = name.replace(/[\[\]]/g, "\\$&");
      var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
        results = regex.exec(url);
      if (!results) return null;
      if (!results[2]) return "";
      return decodeURIComponent(results[2].replace(/\+/g, " "));
    },
    convertRibu(nom) {
      return nom
        ? nom
            .toString()
            .replace(/[^\d]+/g, "")
            .replace(/\B(?=(?:\d{3})+(?!\d))/g, ".")
        : 0;
    }
  }
}).$mount("#app");
