<template>
  <div class="mb-4">
    <label class="block text-grey-darker text-sm font-bold mb-2" :for="idna">
      {{ label }}
    </label>
    <datepicker
      :id="idna"
      :placeholder="label"
      :minimumView="FullDate ? 'date' : 'month'"
      :maximumView="'year'"
      :initialView="'year'"
      :format="FullDate ? 'dd-MM-yyyy' : 'MM-yyyy'"
      :value="tempVal"
      @selected="pushVal"
      :name="idna"
      :disabled="disabled"
      :open-date="openDate"
      :input-class="{
        'border-kp-500': isError,
        'bg-gray-200': disabled,
        'shadow appearance-none border rounded h-12 w-full py-2 px-3 text-grey-darker leading-tight focus:outline-none': true
      }"
      :disabled-dates="disabledDates"
    />
    <ul v-if="isError" class="list-disc list-inside text-kp-500 mt-1">
      <li v-for="(err, i) in errorList" :key="i">
        {{ err.replace(new RegExp(".+(?= field)"), label) }}
      </li>
    </ul>
  </div>
</template>

<script>
import Datepicker from "vuejs-datepicker";

export default {
  props: {
    value: {
      type: String,
      default: null
    },
    label: {
      type: String,
      default: ""
    },
    id: {
      type: String,
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    },
    FullDate: {
      type: Boolean,
      default: false
    },
    hasError: {
      type: [Array, Boolean],
      default() {
        return null;
      }
    },
    openDate: {
      type: [Date, String],
      default() {
        return new Date();
      }
    },
    disabledDates: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  watch: {
    value(val) {
      this.tempVal = val ? new Date(this.value) : val;
      if (this.isError) {
        this.isError = false;
      }
    },
    hasError(val) {
      if (val.length > 0) {
        this.errorList = val;
        this.isError = true;
      } else {
        this.isError = false;
      }
    }
  },
  data() {
    return {
      tempVal: this.value ? new Date(this.value) : this.value,
      isError: this.hasError,
      errorList: []
    };
  },
  components: {
    Datepicker
  },
  computed: {
    idna() {
      return this.id || this.label.replace(/ /, "");
    }
  },
  methods: {
    pushVal(val) {
      this.$emit("input", this.formatDate(val));
      if (this.errorList.length > 0) {
        this.errorList.length = 0;
      }
    },
    formatDate(date) {
      let month = "" + (date.getMonth() + 1);
      let day = "" + date.getDate();
      const year = date.getFullYear();
      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;
      const fixedDate = [year, month];
      if (this.FullDate) {
        fixedDate.push(day);
      }
      return fixedDate.join("-");
    }
  }
};
</script>
