<template>
  <div class="select-container w-full mb-4 relative">
    <label class="block text-sm font-bold mb-2" :for="idna">
      {{ label }}
    </label>
    <div
      class="h-12 flex items-center w-full shadow border rounded cursor-pointer"
      :class="{
        'bg-gray-200': disabled,
        'bg-white': !disabled,
        'border-kp-500': isError
      }"
      ref="buttonSelect"
      :id="idna"
    >
      <select
        class="py-2 px-3 flex-1 focus:outline-none bg-transparent"
        :class="{
          'text-gray-500': tempValue === null
        }"
        v-model="tempValue"
        :disabled="disabled"
        @input="selectList"
      >
        <option :value="null" disabled>
          {{ label }}
        </option>
        <option
          v-for="(list, i) in options"
          :key="i"
          class="text-gray-700"
          :value="list[valueField]"
        >
          {{ list[textField] }}
        </option>
      </select>
      <svg
        class="absolute right-0 w-4 h-4 mr-3"
        ref="iconSelect"
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 207 121.34"
        style="transform: rotate(180deg)"
      >
        <title>Icon</title>
        <path
          d="M629.23,600.67a14.23,14.23,0,0,1-10.29-4.38L540,514.2l-78.93,82.09a14.28,14.28,0,1,1-20.58-19.79l89.22-92.79a14.28,14.28,0,0,1,20.58,0l89.22,92.79a14.28,14.28,0,0,1-10.29,24.17Z"
          transform="translate(-436.5 -479.33)"
        />
      </svg>
    </div>
    <ul v-if="isError" class="list-disc list-inside text-kp-500 mt-1">
      <li v-for="(err, i) in errorList" :key="i">
        {{ err.replace(new RegExp(".+(?= field)"), label) }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: [String, Number],
      default: null
    },
    valueField: {
      type: String,
      default: "id"
    },
    textField: {
      type: String,
      default: "description"
    },
    disabled: {
      type: Boolean,
      default: false
    },
    hasError: {
      type: [Array, Boolean],
      default() {
        return null;
      }
    },
    id: {
      type: String,
      default: null
    },
    label: {
      type: String,
      default: null
    },
    options: {
      type: Array,
      default() {
        return [
          {
            text: "Tidak ada pilihan",
            value: null
          }
        ];
      }
    }
  },
  watch: {
    value(val) {
      this.tempValue = val;
    },
    hasError(val) {
      if (val.length > 0) {
        this.errorList = val;
        this.isError = true;
      } else {
        this.isError = false;
      }
    }
  },
  data() {
    return {
      tempValue: this.value,
      isError: this.hasError,
      errorList: []
    };
  },
  computed: {
    idna() {
      return this.id || this.label.replace(/ /, "");
    }
  },
  methods: {
    selectList(e) {
      if (e.target.value) {
        this.$emit("input", e.target.value);
        if (this.isError) {
          this.isError = false;
        }
      }
    }
  }
};
</script>

<style lang="scss">
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: "";
}
</style>
