<template>
  <div class="mb-4">
    <label class="block text-grey-darker text-sm font-bold mb-2" :for="idna">
      {{ label }}
    </label>
    <input
      v-if="!disabled"
      class="shadow appearance-none border rounded h-12 w-full py-2 px-3 text-grey-darker leading-tight focus:outline-none"
      :id="idna"
      :placeholder="label"
      v-model="tempVal"
      :type="numeric ? 'tel' : 'text'"
      @input="pushVal"
      :class="{ 'border-kp-500': isError }"
    />
    <div
      v-else
      class="flex items-center bg-gray-200 shadow appearance-none border rounded h-12 w-full py-2 px-3 text-grey-darker leading-tight"
      :class="{
        'text-gray-500': !value,
        'border-kp-500': errorList.length > 0
      }"
    >
      {{ value || label }}
    </div>
    <ul v-if="isError" class="list-disc list-inside text-kp-500 mt-1">
      <li v-for="(err, i) in errorList" :key="i">
        {{ err.replace(new RegExp(".+(?= field)"), label) }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: ""
    },
    label: {
      type: String,
      default: ""
    },
    id: {
      type: String,
      default: null
    },
    max: {
      type: String,
      default: null
    },
    numeric: {
      type: Boolean,
      default: false
    },
    noSpecial: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    hasError: {
      type: [Array, Boolean],
      default() {
        return null;
      }
    }
  },
  watch: {
    value(val) {
      if (this.errorList.length > 0) {
        this.errorList.length = 0;
      }
      this.tempVal = val;
    },
    hasError(val) {
      if (val.length > 0) {
        this.errorList = val;
        this.isError = true;
      } else {
        this.isError = false;
      }
    }
  },
  data() {
    return {
      tempVal: this.value,
      isError: this.hasError,
      errorList: []
    };
  },
  computed: {
    idna() {
      return this.id || this.label.replace(/ /, "");
    }
  },
  methods: {
    pushVal(e) {
      let validVal = e.target.value;
      if (this.numeric) {
        validVal = validVal.replace(/[^\d]/g, "");
      }
      if (this.max) {
        validVal = validVal.replace(
          new RegExp("(?<=.{" + this.max + ",}).+", "g"),
          ""
        );
      }
      if (this.noSpecial) {
        validVal = validVal.replace(/[^\da-zA-Z ]/g, "");
      }
      this.tempVal = validVal;
      this.$emit("input", this.tempVal);
      if (this.isError) {
        this.isError = false;
      }
    }
  }
};
</script>
