<script>
export default {
  model: {
    event: "input"
  },
  data() {
    return {
      snapped: false,
      snappedImg: null,
      cameras: [],
      activeCam: 0
    };
  },
  async mounted() {
    if (this.mobileCheck()) {
      this.openFullscreen();
    }
    navigator.getUserMedia =
      navigator.getUserMedia ||
      navigator.webkitGetUserMedia ||
      navigator.mozGetUserMedia ||
      navigator.msGetUserMedia;
    const devices = await navigator.mediaDevices.enumerateDevices();
    this.cameras = devices.filter(device => device.kind === "videoinput");
    navigator.getUserMedia(
      {
        audio: false,
        video: true,
        deviceId: {
          exact: this.cameras[this.activeCam].deviceId
        }
      },
      stream => {
        this.$refs.video.srcObject = stream;
        document.body.style.overflow = "hidden";
        this.$refs.video.play();
      },
      e => {
        return e;
      }
    );
  },
  methods: {
    mobileCheck() {
      var check = false;
      if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        )
      ) {
        check = true;
      }
      return check;
    },
    openFullscreen() {
      if (this.$refs.video.requestFullscreen) {
        document.body.requestFullscreen();
      } else if (document.body.mozRequestFullScreen) {
        /* Firefox */
        document.body.mozRequestFullScreen();
      } else if (document.body.webkitRequestFullscreen) {
        /* Chrome, Safari and Opera */
        document.body.webkitRequestFullscreen();
      } else if (document.body.msRequestFullscreen) {
        /* IE/Edge */
        document.body.msRequestFullscreen();
      }
    },
    closeVideo() {
      if (this.$refs.video) {
        this.$refs.video.srcObject.getTracks().forEach(function(track) {
          track.stop();
        });
      }
      this.$root.isVideo = null;
      document.body.style.overflow = "auto";
    },
    takeSnap() {
      const canvas = this.$refs.canv;
      const video = this.$refs.video;
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      const ctx = canvas.getContext("2d");
      ctx.translate(video.videoWidth, 0);
      ctx.scale(-1, 1);
      ctx.drawImage(video, 0, 0, video.videoWidth, video.videoHeight);
      const jpg = canvas.toDataURL("image/jpeg");
      this.snappedImg = jpg;
      this.snapped = true;
      this.$refs.video.pause();
    },
    sendSnap() {
      this.$emit("input", this.snappedImg);
      this.closeVideo();
    },
    async retake() {
      this.snapped = false;
      this.snappedImg = null;
      this.$refs.video.play();
    },
    changeCamera() {
      this.$refs.video.srcObject.getTracks().forEach(function(track) {
        track.stop();
      });
      this.activeCam = this.activeCam ? 0 : 1;
      navigator.getUserMedia(
        {
          audio: false,
          video: true,
          deviceId: {
            exact: this.cameras[this.activeCam].deviceId
          }
        },
        stream => {
          this.$refs.video.srcObject = stream;
          this.$refs.video.play();
        },
        e => {
          return e;
        }
      );
    }
  }
};
</script>

<style lang="css" scoped>
video {
