<script>
import Invoice from "./Invoice.vue";
import { getters, store } from "../store";

export default {
  components: {
    Invoice
  },
  data() {
    return {
      collapseStep: Object.keys(store.stepName).reduce((fixCol, stepNa) => {
        fixCol[stepNa] = false;
        return fixCol;
      }, {}),
      tncPop: false
    };
  },
  computed: {
    dataReview() {
      return getters.dataReview();
    },
    stepName() {
      return store.stepName;
    }
  },
  methods: {
    goToStep(step) {
      if (
        step === "personal-page" ||
        step === "address-page" ||
        step === "contact-page"
      ) {
        if (step === "personal-page") {
          this.$root.$children[0].$children[3].cur = 0;
        } else if (step === "address-page") {
          this.$root.$children[0].$children[3].cur = 1;
        } else if (step === "address-page") {
          this.$root.$children[0].$children[3].cur = 2;
        }
        this.$root.cur = 0;
      } else if (step === "emergency-contact-page") {
        this.$root.$children[0].$children[3].cur = 2;
        this.$root.cur = 1;
      } else if (step === "job-page" || step === "address-job-page") {
        if (step === "job-page") {
          this.$root.$children[0].$children[5].cur = 0;
        } else if (step === "address-job-page") {
          this.$root.$children[0].$children[5].cur = 1;
        }
        this.$root.cur = 2;
      } else {
        this.$root.$children[0].$children[5].cur = 1;
        this.$root.cur = 3;
      }
    }
  }
};
</script>

